<template>
  <div class="zizhushegnbao">
    <meta
      name="viewport"
      content="width=device-width,initial-scale=1.0, minimum-scale=1.0, maximum-scale=1.0, user-scalable=no"
    />
    <div class="main" v-show="!show">
      <div class="title">
        <van-button
          style="background-color: #565353 !important; border: 0"
          to="/"
          icon="arrow-left"
          type="primary"
        /><span style="margin: 0 auto">货车申报</span>
      </div>
      <van-field
        v-model="formValute.real_name"
        class="zizhushengbaoFiled"
        label-class="zizhushengbaoLabel"
        placeholder="请输入姓名"
        :rules="[{ required: true, message: '请填写姓名' }]"
        label="姓名："
      />
      <van-field
        v-model="formValute.id_card"
        class="zizhushengbaoFiled"
        label-class="zizhushengbaoLabel"
        @input="
          () => {
            this.formValute.id_card = this.formValute.id_card.replace(
              /[^\w\.\/]/g,
              ''
            );
          }
        "
        placeholder="请输入正确的证件号"
        :rules="[{ validator: asyncValidator, message: '请输入正确的证件号' }]"
        label="身份证号："
      />
      <LicenseKeyboard
        v-model="queryData.licenseNumber"
        @confirm="handleInput"
        ref="License"
      >
        <van-field
          @click="jianting"
          v-model="formValute.plate_number"
          class="zizhushengbaoFiled"
          label-class="zizhushengbaoLabel"
          clearable
          readonly
          placeholder="请选择车牌号"
          :rules="[{ required: true, message: '请填写姓名' }]"
          label="车牌号："
        >
          <template #extra>
            <van-icon
              name="cross"
              v-if="formValute.plate_number"
              style="font-size: 1.125rem"
              @click.stop="clearPlateNumber"
            />
          </template>
        </van-field>
      </LicenseKeyboard>
      <van-field
        v-model="formValute.phone"
        class="zizhushengbaoFiled"
        label-class="zizhushengbaoLabel"
        center
        clearable
        type="number"
        placeholder="请输入手机号"
        :rules="[{ required: true, message: '请填写手机号' }]"
        label="手机号："
      >
      </van-field>
      <van-field
        v-model="formValute.sms_code"
        class="zizhushengbaoFiled"
        :placeholder="
          formValute.phone === '' ? '请先输入手机号' : '请输入验证码'
        "
        :readonly="!formValute.phone"
        label-class="zizhushengbaoLabel"
        center
        clearable
        label="验证码："
      >
        <template #button>
          <van-button
            size="small"
            type="primary"
            native-type="button"
            @click="getsms"
            v-if="!smsIF"
            >获取验证码</van-button
          >
          <van-button
            size="small"
            type="primary"
            native-type="button"
            class="gray"
            disabled
            v-else
            >获取验证码<span>{{ smsdJS }}</span></van-button
          >
        </template>
      </van-field>

      <van-button
        type="primary"
        block
        class="submit"
        @click="onSubmit"
        native-type="submit"
        >提交</van-button
      >

      <!-- </van-form> -->
      <van-dialog
        v-model="showPickerUploader"
        title="点击下面方框选择行程码"
        show-cancel-button
      >
        <van-uploader
          v-model="fileList"
          max-count="1"
          :after-read="afterRead"
        />
      </van-dialog>
    </div>
    <van-overlay :show="overlay">
      <van-loading
        size="24px"
        vertical
        style="
          position: fixed;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        "
        >正在校验姓名和身份证号码</van-loading
      >
    </van-overlay>
    <newsuccess
      :dataList="formValute"
      :dataList_text="formValute_text"
      :fileList="fileList"
      :id_verify_result="id_verify_result"
      :successShow.sync="successShow"
      :successInfo_car="successInfo_car"
      :getRoute="routerName"
      @closeSparingContent="closeSparingContent($event)"
      @Back="back($event)"
      v-if="show"
      class="fixedPOstion"
    ></newsuccess>
  </div>
</template>

<script>
import cookies from '../lib/util.cookie'
import VDistpicker from 'v-distpicker'
import { areaList } from '@vant/area-data'
import
newsuccess
  from '../components/newsuccess.vue'
import
licensePlate from '../components/licensePlate.vue'
import {
  Toast
} from 'vant'
import {
  getAreaUserApi, POSTOwndecomeBackApi, getSmsCode, uploadImg, getOowndeclare
} from '@/common/api/api.js'
import { mixinsChaXun } from '../common/mixinsChaXun'
import {
  goBackAndSetCookies
} from '../common/goBackAndSetCookies'
import Vue from 'vue'
export default {
  components: { VDistpicker, newsuccess, licensePlate },
  mixins: [mixinsChaXun, goBackAndSetCookies],
  data () {
    return {
      routerName: 'truckDeclaration',
      beifenPlateNUmber: '',
      queryData: {
        licenseNumber: true
      },
      successInfo: {},
      successShow: false,
      isHide: false,
      show: false,
      areaList,
      cascaderValue: '',
      juzhudiValue: '',
      columns: [

      ],
      loading: true,
      showDiagler: false,
      smsCode: '',
      minDate: new Date(2020, 0, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date(2021, 0, 17),
      idCardTypeColums: [
        {
          value: 'id',
          text: '身份证'
        },
        {
          value: 'passport',
          text: '护照号'
        },
        {
          value: 'officer',
          text: '军官证号'
        }
      ],
      showPickerUploader: false,
      columnsJuzhudi: [],
      showPicker: false,
      showPicker2: false,
      showPickerDate: false,
      showPickerDate_fh: false,
      showPicker_idCardType: false,
      showPicker_juzhudi: false,
      fileList: [],
      formValute: {
        real_name: '',
        phone: '',
        id_card: '',
        has_report: 0
      },
      formValute_text: {
        province: '',
        city: '',
        street: '',
        county: ''
      },
      checked: false,
      value: '',
      sms: '',
      smsIF: false,
      setTime: '',
      columns2: [],
      smsdJS: 60,
      newData: [],
      selectuID_FirstList: [],
      selectList_FirstIndex: [],
      selectList_SecondIndex: [],
      levelTwo_BySelect_GetList_Is: [],
      selectListSecond: [],
      acData_second: 0,
      acData: 0,
      isFirst: false,
      successInfo_car: {},
      msg: 1
    }
  },
  created () {
    // 获取全国4级联动
    this.isFirst = true
  },
  computed: {
  },
  destroyed () {
    document.removeEventListener('click', (ev) => {
      if (ev.target.innerHTML === '关闭') {
        Vue.set(this.formValute, 'plate_number', this.beifenPlateNUmber)
        this.$forceUpdate()
      }
    })
  },
  mounted () {
    window.addEventListener('click', (ev) => {
      if (ev.target.innerHTML === '关闭') {
        Vue.set(this.formValute, 'plate_number', this.beifenPlateNUmber)
        this.$forceUpdate()
      }
    })
  },
  methods: {
    onClose () {
      console.log(this.$refs.License.close())
    },
    //
    jianting () {
      if (this.formValute.plate_number) {
        this.beifenPlateNUmber = this.formValute.plate_number
        Vue.set(this.formValute, 'plate_number', '')
        this.$forceUpdate()
      }
    },
    clearPlateNumber () {
      // console.log(this.formValute)
      // this.formValute.plate_number = ''
      Vue.set(this.formValute, 'plate_number', '')
      this.$forceUpdate()
      console.log(this.formValute)
    },
    handleInput (val) {
      this.formValute.plate_number = val
    },
    setSuccessInfoNull () {
      cookies.set('successInfo_clareTravel', null)
      this.successInfo_car = null
    },
    closeSparingContent (show) {
      this.show = show
    },
    imagevalidator () {
      if (this.formValute.image) {
        return true
      } else {
        return false
      }
    },
    onInput (checked) {
      checked ? this.formValute.has_report = 1 : this.formValute.has_report = 0
    },
    onSubmit (values) {
      let formValute = this.formValute
      if (!formValute.real_name) {
        Toast.fail('请输入姓名')
        return false
      }

      var pat = new RegExp('[^a-zA-Z0-9\_\u4e00-\u9fa5]', 'i')
      let realNameChilaReg = /\d+|\w+/
      if (pat.test(formValute.real_name)) {
        console.log(formValute.real_name, pat.test(formValute.real_name))
        Toast.fail('姓名不能包含特殊字符')
        return false
      }
      if (this.formValute.card_type === 'id') {
        if (realNameChilaReg.test(formValute.real_name)) {
          Toast.fail('姓名不能包含字母或数字')
          return false
        }
      }
      if (!formValute.id_card) {
        Toast.fail('请输入证件号')
        return false
      }
      let idCardReg = /^[1-9]\d{5}(18|19|20)\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/// 先用正则判断是否为正确的格式
      if (this.formValute.card_type === 'id') {
        if (!idCardReg.test(formValute.id_card)) {
          Toast.fail('证件号格式错误')
          return false
        } else {
          console.log('校验')
        }
      }
      if (!formValute.plate_number) {
        Toast.fail('请输入车牌号')
        return false
      }
      if (!formValute.phone) {
        Toast.fail('请输入手机号')
        return false
      }
      let myreg = /^1\d{10}$/
      if (!myreg.test(formValute.phone)) {
        Toast.fail('手机号格式错误')
        return false
      }
      if (!formValute.sms_code) {
        Toast.fail('请输入验证码')
        return false
      }
      if (formValute.sms_code.length < 6) {
        Toast.fail('验证码长度不足6位')
        return false
      }
      this.qgrkk({
        real_name: this.formValute.real_name,
        id_card: this.formValute.id_card
      })
      this.setSuccessInfoNull()
    },
    getsms () {
      getSmsCode({
        prefix: 'declare_verify',
        phone: this.formValute.phone
      }).then(res => {
        if (res.code === 200) {
          this.smsIF = true
          this.showDiagler = true
          this.djsSms()
          Toast.success(res.msg)
        } else {
          Toast.fail(res.msg)
        }
      })
    },
    djsSms () {
      this.smsdJS = this.smsdJS - 1
      this.smsIF = true
      if (this.smsdJS <= 0) {
        this.smsIF = false
        this.smsdJS = 60
        clearTimeout(this.setTime)
      } else {
        this.setTime = setTimeout(() => {
          this.djsSms()
        }, 1000)
      }
    },
    afterRead (file) {
      file.status = 'uploading'
      file.message = '上传中...'
      console.log(file)
      uploadImg({
        file: file.content
      }).then(res => {
        if (res.code === 200) {
          console.log(res.data)
          this.formValute.travel_img = res.data.src
          file.status = 'done'
          file.message = '上传成功'
        } else {
          file.status = 'failed'
          file.message = '上传失败'
        }
      }).catch(res => {
        file.status = 'failed'
        file.message = '上传失败'
      })
    },
    choose () {
      this.show = !this.show
    },
    goBack () {
      this.$router.back(-1)
    },
    onChangeProvince (a) {
      console.log(a)
      this.formValute.laiyuandi.province = a
    },
    onConfirm_Date (date) {
      this.showPickerDate = false
      this.formValute.arrival_time = this.$dateTimeFilters(date, 'yyyy-MM-dd')
    },
    asyncValidator (val) {
      if (this.formValute.card_type_text === '身份证') {
        return /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/.test(val)
      } else {
        return true
      }
    },
    eventIfSelect () {
      // 若选择了证件类型
      if (this.formValute.card_type_text) {

      } else {
        Toast.fail('请先选择证件类型')
      }
    },
    back () {
      this.$router.push({
        path: '/'
      })
    }
  }
}
</script>
<style>
.van-field__control:disabled {
  color: black;
  cursor: not-allowed;
  opacity: 1;
  -webkit-text-fill-color: black;
}
.van-field--disabled .van-field__label {
  color: #646566;
}
.zizhushegnbao ::-webkit-input-placeholder {
  color: red !important;
  font-weight: 500 !important;
}

/* Firefox 4-18 */
.zizhushegnbao :-moz-placeholder {
  color: red !important;
  font-weight: 500 !important;
}

/* Firefox 19-50 */
.zizhushegnbao ::-moz-placeholder {
  color: red !important;
  font-weight: 500 !important;
}

/* - Internet Explorer 10–11
- Internet Explorer Mobile 10-11 */
.zizhushegnbao :-ms-input-placeholder {
  color: red !important;
  font-weight: 500 !important;
}

/* Edge (also supports ::-webkit-input-placeholder) */
.zizhushegnbao ::-ms-input-placeholder {
  color: red !important;
  font-weight: 500 !important;
}

/* CSS Working Draft */
.zizhushegnbao ::placeholder {
  color: red !important;
  font-weight: 500 !important;
}
.distpicker-address-wrapper .address-container ul li.active {
  color: red !important;
}
.distpicker-address-wrapper .address-container ul li {
  font-size: 14px;
}
.van-dialog__content {
  display: flex;
  justify-content: center;
}
.van-uploader__upload {
  width: 10rem;
  height: 10rem;
}
.distpicker-address-wrapper {
  color: black;
}
.cellClass_spring > .van-cell__value {
  min-width: 75%;
}
.van-field__control {
  font-size: 14px !important;
}
.van-nav-bar__right {
  padding: 5px;
}
.van-tabs--card > .van-tabs__wrap {
  height: 36px;
}
.van-tabs__nav--card {
  margin: 0;
  border: none;
  height: 36px;
}
.van-tabs__nav--card .van-tab {
  color: #333333;
  border-right: none;
  height: 36px;
}
.van-tabs__nav--card .van-tab.van-tab--active {
  color: #fff;
  background-color: #ff5318;
}
.zizhushengbaoLabel {
  /* color: red !important; */
}
.van-field__label {
  width: auto;
}
input:disabled {
  background-color: #d5d5d5 !important;
}
</style>
<style  scoped>
.van-form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.submit {
  margin-top: 1.8rem;
  border-radius: 0.3125rem;
}
.title {
  font-size: 18px;
  color: #fff;
  display: flex;
  align-items: center;
  /* justify-content: space-between; */
  width: 95%;
  margin-bottom: 1rem;
}
.zizhushegnbao {
  min-height: 100vh;
  background: #565353;
  display: flex;
  /* justify-content: center; */
  align-items: center;
  flex-direction: column;
  padding: 0.9375rem;
}
.zizhushengbaoFiled {
  border-radius: 0.375rem;
  background-color: #d5d5d5;
  /* width: 95%; */
  display: flex;
  align-items: center;
  /* width: 23.75rem; */
  min-height: 2.8125rem;
  margin-bottom: 0.9375rem;
}
.canYouHandBaoGao {
  color: #68b0f9;
  font-size: 1rem;
  margin-right: 2rem;
}
.dibu {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.divwrap {
  height: 400px;
  overflow-y: auto;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
}
.divwrap >>> .distpicker-address-wrapper {
  color: #999;
}
.divwrap >>> .address-header {
  position: fixed;
  bottom: 400px;
  width: 100%;
  background: #000;
  color: #fff;
}
.divwrap >>> .address-header ul li {
  flex-grow: 1;
  text-align: center;
}
.divwrap >>> .address-header .active {
  color: #fff;
  border-bottom: #666 solid 8px;
}
.divwrap >>> .address-container .active {
  color: #000;
}
.pickerUploderPopup {
  width: 80%;
  height: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: rgb(225, 226, 225);
}
.fixedPOstion {
  position: absolute;
  top: 0;
  left: 0;
  min-height: 100vh;
  /* height: 100%; */
  background: #565353;
  width: 100%;
  padding: 0 0.5rem;
  box-sizing: border-box;
  padding-top: 0.3125rem;
  overflow: hidden;
}
</style>



